
.active {
  color: #2299DD;
  font-weight: bold;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.MyQuesition_header {
  width: 100%;
  height: 60px;
  border: 1px solid #EBEEF5;
  line-height: 58px;
  background-color: #f5f7fa;
}
.MyQuesition_header span:nth-of-type(1) {
  font-size: 25px;
  font-family: "";
  color: black;
  padding-left: 20px;
}
.MyQuesition_header span:nth-of-type(1)::before {
  content: "\e9f9";
  font-family: "icomoon";
  color: gray;
  padding-right: 10px;
  line-height: 50px;
}



