
.active {
  color: #2299DD;
  font-weight: bold;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.list_main {
  height: 580px;
  width: 100%;
  margin-top: -25px;
}
.list {
  width: 1100px;
  background-color: white;
  height: 500px;
  border-radius: 10px;
  margin-top: 20px;
  overflow: hidden;
  float: left;
}
.unite {
  width: 100%;
  height: 420px;
}
.waterfull {
  float: left;
  width: 100%;
  padding-left: 10px;
  overflow: hidden;
}
.waterfull .item {
  float: left;
  width: 23%;
  margin-left: 10px;
  margin-top: 20px;
  position: relative;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  overflow: hidden;
  white-space: nowrap;
}
.waterfull .item img {
  width: 100%;
  height: 200px;
  cursor: pointer;
  border-radius: 5px;
  -webkit-box-shadow: 0px 5px 10px black;
          box-shadow: 0px 5px 10px black;
}
.waterfull .item .list_describe {
  bottom: -82%;
  position: absolute;
  font-size: 15px;
  width: 100%;
  max-width: 290px;
  height: 99%;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  border-radius: 5px;
  cursor: pointer;
}
.waterfull .item .list_describe i {
  color: white;
  font-size: 15px;
  padding-right: 5px;
  position: absolute;
  bottom: 10px;
  right: 5px;
}
.waterfull .item:hover {
  transform: scale(1.05, 1.05);
  -webkit-transform: scale(1.05, 1.05);
  -o-transform: scale(1.05, 1.05);
  -ms-transform: scale(1.05, 1.05);
  -moz-transform: scale(1.05, 1.05);
}
.waterfull .item:hover .shop_name {
  margin-top: 20%;
}
.waterfull .item:hover .list_describe {
  transform: translate(0%, -85%);
  -webkit-transform: translate(0%, -85%);
  -ms-transform: translate(0%, -85%);
  -moz-transform: translate(0%, -85%);
  -o-transform: translate(0%, -85%);
  background: rgba(0, 0, 0, 0.7);
}
.waterfull .item .shop_name {
  margin-top: -2px;
  width: auto;
  display: inline-block;
  color: #fff;
  font-size: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 10px;
  shape-outside: border-box;
  float: left;
}
.waterfull .item .shop_der {
  margin-top: -2px;
  width: auto;
  display: inline-block;
  color: #909399;
  font-size: 15px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 10px;
  margin-top: 35%;
}
.title {
  font-size: 28px;
  font-weight: bold;
  margin-top: 20px;
  margin-left: 10px;
  padding-top: 10px;
}
.title i {
  color: #2299DD;
  font-size: 28px;
}
.lunbo {
  margin-top: 5px;
  float: left;
  width: 350px;
  background-color: white;
  border-radius: 10px;
  margin-top: 20px;
  overflow: hidden;
  margin-left: 30px;
}
.lunbo .active_title {
  width: 100%;
  height: 40px;
  font-weight: bold;
  font-size: 25px;
  text-align: center;
  border-bottom: 1px solid #EBEEF5;
  line-height: 40px;
}
.rank {
  margin-top: -10px;
  height: 470px;
  padding-left: 10px;
}
.rank p {
  display: inline-block;
  margin-top: 30px;
  color: gray;
  font-weight: bold;
  width: 20px;
  height: 20px;
  text-align: center;
}
.rank .use_portrait {
  width: 14%;
  height: 2%;
  max-width: 50px;
  max-height: 50px;
  vertical-align: middle;
  margin-left: 10px;
  border-radius: 50px;
}
.rank .use_name {
  display: inline-block;
  width: 50%;
  height: 40px;
  padding-left: 25px;
  padding-top: 20px;
  font-size: 18px;
  font-weight: bold;
}
.rank [class^="el-icon"] {
  color: #ff5500;
  font-size: 30px;
}
.rank li:nth-of-type(1) p,
.rank :nth-of-type(2) p,
.rank :nth-of-type(3) p {
  background: #2299DD;
  line-height: 20px;
  color: white;
  border-radius: 5px;
}
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}



